@import "../variables.scss" ;

.mg-Card {
  width: $card-width;
  height: $card-height;
  box-shadow: 0px 5px 20px 0px rgba(51, 73, 94, 0.05);
  transition: box-shadow 0.2s cubic-bezier(0.42, 0, 0.58, 1);
  margin: 0 $card-margin-right 30px 0;
  transform: translateZ(0);

  position: relative;
  cursor: pointer;

  &:last-child {
    margin-right: 0;
  }

  &:hover {
    box-shadow: 0px 10px 40px 0px rgba(51, 73, 94, 0.15);
  }

  &.rotate {
    &-card {
      display: block;
      color: #4f4f4f;
      z-index: 2;
      backface-visibility: hidden;
      width: $card-width;
      height: $card-height;
      
      transition: 0.6s;
      transform-style: preserve-3d;
      
      position: absolute;
      top: 0;
      transform: rotateY(180deg);
      left: 0;

      &:after {
        content: "Card";
        transform: rotateY(180deg);
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: 3em;
        font-weight: lighter;
        backface-visibility: hidden;
        width: $card-width;
        height: $card-height;
        
        transition: 0.6s;
        transform-style: preserve-3d;
        
        position: absolute;
        top: 0;
        left: 0;
      }
    }
  }

  &.rotate a,
  &.rotate a:after {
    transform: rotateY(180deg);
  }

  a,
  a:after {
    backface-visibility: hidden;
    width: $card-width;
    height: $card-height;

    transition: 0.6s;
    transform-style: preserve-3d;

    position: absolute;
    top: 0;
    left: 0;
  }

  a {
    display: block;
    color: #4f4f4f;
    z-index: 2;

    &:after {
      content: "Card";
      transform: rotateY(180deg);
      display: flex;
      align-items: center;
      justify-content: center;
      font-size: 3em;
      font-weight: lighter;
    }
  }

  h2 {
    height: $card-title-height;
    line-height: $card-title-height;
    text-align: center;
    letter-spacing: 0.9px;
    font-weight: lighter;
    margin: 0;

    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
  }

  .card-icon {
    display: flex;
    align-items: center;
    justify-content: center;
    height: $card-icon-height;
    padding: 10%;
    box-sizing: border-box;
  }
}
